import { reactive } from 'vue'

interface Rules {
  username: {
    message: string
    required: boolean
    trigger: string
  }[]
  password: (
    | {
        message: string
        required: boolean
        trigger: string
        min?: undefined
        max?: undefined
      }
    | {
        min: number
        max: number
        message: string
        trigger: string
        required?: undefined
      }
  )[]
}

interface LoginUser {
  username: string
  password: string
}

// only to test export func
export function test_export() {}

// 参考：https://element-plus.gitee.io/zh-CN/component/form.html#%E8%87%AA%E5%AE%9A%E4%B9%89%E6%A0%A1%E9%AA%8C%E8%A7%84%E5%88%99
export const login_user = reactive<LoginUser>({ username: '', password: '' })

export const login_rules = reactive<Rules>({
  username: [
    {
      message: '用户名不能为空...',
      required: true,
      trigger: 'blur' // 元素失去焦点的时候触发
    }
  ],
  password: [
    {
      message: '密码不能为空...',
      required: true,
      trigger: 'blur'
    },
    {
      min: 5,
      max: 15,
      message: '密码长度需要在5-15个字符个数之间...',
      trigger: 'blur'
    }
  ]
})
